import React from "react";
import { Tab } from "@ya.praktikum/react-developer-burger-ui-components";
import styles from "./burger-ingredients_tab.module.css"

function BurgerIngingredientsTab() {

  const [current, setCurrent] = React.useState('one')

  return (
  <>
    <h1 className={styles.main_title + " text_type_main-large" + ' pt-10 pb-5'}> </h1>

    <div style={{ display: 'flex' }} className={'pb-10'}>
      <Tab value="" active={current === ''} onClick={setCurrent}>
        
      </Tab>
      <Tab value="" active={current === ''} onClick={setCurrent}>
        
      </Tab>
      <Tab value="" active={current === ''} onClick={setCurrent}>
        
      </Tab>
    </div>
  </>
  )  
}

export default BurgerIngingredientsTab 